 -1;\n  top: -8px;\n  left: -8px;\n  right: -8px;\n  bottom: -8px;\n  border-width: 2px;\n  border-style: solid;\n}\n@mixin btn-hover-global {\n  box-shadow: 2px 4px $shadow;\n  transform: translate(2px, 2px);\n}\n\n// Ultimate member buttons\ninput[type=\"submit\"].um-button {\n  border: 2px solid $aqua-dark !important;\n}\n\n.um-button.um-alt {\n  border: 2px solid $green-dark !important;\n}\n\n//Primary $aqua CTA Button\n\n.btn-primary, input[type=\"submit\"].um-button {\n  @include btn-global;\n\n  background-color: $aqua;\n  border-color: $aqua-light;\n  text-shadow: 4px 2px $aqua-dark;\n\n  .um.um & {\n    border: 8px solid $aqua-light !important;\n\n    &:hover {\n      border: 8px solid $aqua !important;\n    }\n\n    &:active, &:focus {\n      border: 8px solid $aqua-dark !important;\n    }\n  }\n\n  &::before {\n    @include btn-before-global;\n\n    border-color: $aqua-dark;\n  }\n\n  &:hover {\n    @include btn-hover-global;\n\n    background-color: $aqua-light;\n    border-color: $aqua;\n  }\n\n  &:active, &:focus {\n    background-color: $aqua;\n    border-color: $aqua-dark;\n    box-shadow: 0 0;\n  }\n}\n\n//Secondary $green-med CTA button\n\n.btn-secondary, .um-button.um-alt, .wpcf7-submit {\n  @include btn-global;\n\n  background-color: $green-med;\n  border-color: $green-light;\n  text-shadow: 4px 2px $green-dark;\n\n  &::before {\n    @include btn-before-global;\n\n    border-color: $green-dark;\n  }\n\n  &:hover {\n    @include btn-hover-global;\n\n    background-color: $green-light;\n    border-color: $green-med;\n  }\n\n  &:active, &:focus {\n    background-color: $green-med;\n    border-color: $green-dark;\n    box-shadow: 0 0;\n  }\n}\n\n//Tertiary $orange-med CTA Button\n\n.btn-tertiary {\n  @include btn-global;\n\n  background-color: $orange-med;\n  border-color: $orange-light;\n  text-shadow: 4px 2px $orange-dark;\n\n  &::before {\n    @include btn-before-global;\n\n    border-color: $orange-dark;\n  }\n\n  &:hover {\n    @include btn-hover-global;\n\n    background-color: $orange-light;\n    border-color: $orange-med;\n  }\n\n  &:active, &:focus {\n    background-color: $orange-med;\n    border-color: $orange-dark;\n    box-shadow: 0 0;\n  }\n}\n","input {\n  font-family: $font-family-serif;\n  color: $aqua-dark;\n}\n\nlabel {\n  @extend h6;\n}\n\n.page-submit-new-observation-data {\n  div[id=\"map_canvas\"] {\n    width: 100% !important;\n  }\n}\n\n.wpcf7-textarea {\n  @extend .materialize-textarea;\n}\n\n.wpcf7-form-control-wrap.photo {\n  display: block;\n}\n\n.wpcf7-latlong {\n  display: none;\n}\n\n.wpcf7-form {\n  fieldset {\n    position: relative;\n    border: 1px solid $navy;\n    padding: 10px 10px 0;\n    margin-bottom: 20px;\n    background: $ice;\n  }\n\n  .row {\n    position: relative;\n    margin: 0 0 10px;\n    border: 1px solid $navy;\n    background: white;\n    padding: 20px;\n  }\n}\n",".observation.card {\n  box-shadow: 8px 8px $shadow;\n  border-radius: 0;\n  margin: 20px 0;\n  position: relative;\n  color: $navy;\n\n  a.mega-link {\n    display: block;\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    z-index: 100;\n  }\n\n  img {\n    border: 2px solid white;\n    margin: 13px !important;\n  }\n\n  &.horizontal {\n    img {\n      max-width: 75px;\n      height: auto;\n    }\n  }\n\n  .card-content {\n    padding: 10px 0;\n  }\n\n  .material-icons {\n    font-size: 18px;\n    vertical-align: top;\n  }\n\n  h3 {\n    font-family: $font-family-sans-serif;\n    font-size: 18px;\n    margin: 5px 0;\n    color: white;\n\n    a {\n      color: white;\n    }\n  }\n\n  p {\n    font-size: 14px;\n  }\n\n  ul {\n    margin: 0;\n    padding: 0;\n  }\n\n  li {\n    font-size: 12px;\n    display: inline-block;\n\n    &:first-child {\n      padding-right: 20px;\n    }\n  }\n\n  // 1, 5, 9, etc\n  &:nth-child(4n-7) {\n    background: $orange-med;\n    border: 1px solid $orange-dark;\n\n    h3 a {\n      text-shadow: 2px 2px $orange-dark;\n    }\n