$orange-med;\n    }\n\n    &:active {\n      background-color: $orange-med;\n      border: 6px solid $orange-dark;\n      box-shadow: 0 0;\n    }\n  }\n\n  &.event {\n    background: $orange-med;\n\n    .date {\n      background-color: $orange-med;\n      border: 6px solid $orange-light;\n\n      &::before {\n        border: 2px solid $orange-dark;\n      }\n    }\n\n    .month {\n      text-shadow: 4px 2px $orange-dark;\n    }\n\n    .day {\n      text-shadow: 4px 2px $orange-dark;\n    }\n\n    .title {\n      border-top: 9px solid $orange-light;\n    }\n  }\n}\n//Ornithology ($red-med)\n\n.ornithology {\n  .learn-more-btn {\n    @include learn-more-btn;\n\n    background-color: $red-med;\n    border: 6px solid $red-light;\n    text-shadow: 4px 2px $red-dark;\n\n    &::before {\n      @include learn-more-btn-before;\n\n      border: 2px solid $red-dark;\n    }\n\n    &:hover {\n      @include learn-more-btn-hover;\n\n      background-color: $red-light;\n      border: 6px solid $red-med;\n    }\n\n    &:active {\n      background-color: $red-med;\n      border: 6px solid $red-dark;\n      box-shadow: 0 0;\n    }\n  }\n\n  &.event {\n    background: $red-med;\n\n    .date {\n      background-color: $red-med;\n      border: 6px solid $red-light;\n\n      &::before {\n        border: 2px solid $red-dark;\n      }\n    }\n\n    .month {\n      text-shadow: 4px 2px $red-dark;\n    }\n\n    .day {\n      text-shadow: 4px 2px $red-dark;\n    }\n\n    .title {\n      border-top: 9px solid $red-light;\n    }\n  }\n}\n//Botany ($green-med)\n\n.botany {\n  .learn-more-btn {\n    @include learn-more-btn;\n\n    background-color: $green-med;\n    border: 6px solid $green-light;\n    text-shadow: 4px 2px $green-dark;\n\n    &::before {\n      @include learn-more-btn-before;\n\n      border: 2px solid $green-dark;\n    }\n\n    &:hover {\n      @include learn-more-btn-hover;\n\n      background-color: $green-light;\n      border: 6px solid $green-med;\n    }\n\n    &:active {\n  